import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import { NavLink, useParams } from 'react-router-dom';
import "./Videolistpage.css";
import { useUIProvider } from '../../context/Context';
import { List, ListItem } from '@mui/material';

const Videolistpage = () => {
    const { course_id } = useParams()
    const [videoList, setVideoList] = useState([]);
    const [videoSourceUrl, setVideoSourceUrl] = useState("");
    const [videoSourceTitle, setVideoSourceTitle] = useState("");
    const { toggle } = useUIProvider();
    const videoref = useRef();

    const getVideoListData = async (id) => {
        axios.get(`/admin/dashboard/view_course/${id}`)
            .then(res => {
                setVideoList(res.data);
            })
            .catch(err => {
                console.log(err);
            })
    }

    const onActive = (title, url) => {
        setVideoSourceUrl(url);
        setVideoSourceTitle(title);
        videoref.current?.load();
    }

    useEffect(() => {
        toggle && document.querySelector(".video_list_page").classList.add("active");
        toggle && document.querySelectorAll(".video_list_page_head").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".video_list_page_des").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".video_list_page_container_right_lists").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".no_video").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".video_list_page").classList.remove("active");
        !toggle && document.querySelectorAll(".video_list_page_head").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".video_list_page_des").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".video_list_page_container_right_lists").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".no_video").forEach(e => e.classList.remove("active"));

        getVideoListData(course_id);
    }, [videoSourceUrl, videoSourceTitle, toggle, videoref]);

    return (
        <div className="video_list_page">
            <div className="video_list_page_container">
                <div className="video_list_page_left">
                    <div className="video_list_page_left_container">
                        <div className="video_list_page_left_video_box">
                            <video
                                ref={videoref}
                                controlsList="nodownload norepeat"
                                controls
                            >
                                <source
                                    src={`/public/videos/${videoSourceUrl}`}
                                    type="video/mp4"
                                />
                                <source
                                    src={`/public/videos/${videoSourceUrl}`}
                                    type="video/mkv"
                                />
                                Your browser does not support the video tag.
                            </video>
                        </div>
                        <h3 className='video_list_page_head'># {videoSourceTitle ? videoSourceTitle : `Select a video`}</h3>
                        <p
                            className='video_list_page_des'
                            style={{
                                marginBottom: "20px"
                            }}
                        >{videoList.courseDescription}</p>
                    </div>
                </div>
                <div className="video_list_page_right">
                    <List
                        sx={{
                            width: "100%",
                            height: "3rem",
                        }}>
                        {
                            videoList.videolist && Object.keys(videoList.videolist).length ?
                                videoList.videolist.map(e => {
                                    return (
                                        <NavLink
                                            key={e.videoid}
                                            style={{
                                                textDecoration: "none",
                                                width: "100%",
                                                height: "3rem",
                                            }}
                                        >
                                            <ListItem
                                                className="video_list_page_container_right_lists"
                                                sx={{
                                                    width: "100%",
                                                    height: "3rem",
                                                }}
                                                onClick={() => {
                                                    onActive(e.videoTitle, e.videoUrl);
                                                    getVideoListData(course_id);
                                                }}
                                                key={e.videoid}
                                            >
                                                {e.videoTitle}
                                            </ListItem>
                                        </NavLink>
                                    )
                                })
                                :
                                <span
                                    className='no_video'
                                    style={{
                                        fontSize: "20px",
                                        fontFamily: "cursive",
                                    }}
                                >No Videos Yet</span>
                        }
                    </List>
                </div>
            </div>
        </div>
    )
}

export default Videolistpage;