import React from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import FormatAlignCenterIcon from '@mui/icons-material/FormatAlignCenter';
import "./Navbaar.css";
import { useUIProvider } from '../../context/Context';
import DarkModeIcon from '@mui/icons-material/DarkMode';

const Navbaar = ({ drawerFun }) => {
    const { setDrawerAdminOpen, darkLightMode, toggle } = useUIProvider();

    return (
        <>
            <nav className='header'>
                <button
                    className='drawer_btn'
                    onClick={drawerFun}
                >
                    <MenuIcon />
                </button>
                <div className="header_container_right">
                    <button
                        className="admin_dashboard_drawer_right"
                        onClick={() => setDrawerAdminOpen(true)}
                    >
                        <FormatAlignCenterIcon />
                    </button>
                    <button
                        className='dark_mode_btn'
                        onClick={darkLightMode}
                    >
                        {
                            toggle
                                ?
                                <Brightness7Icon />
                                :
                                <DarkModeIcon />
                        }
                    </button>
                </div>
            </nav>
        </>
    )
}

export default Navbaar;