import React, { useEffect, useState } from 'react';
import "./Dashboard.css";
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import Piechart from "../../components/piechart/Piechart";
import AdminRight from '../../components/adminright/AdminRight';
import DashboardIcon from '@mui/icons-material/Dashboard';
import PersonIcon from '@mui/icons-material/Person';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import VisibilityIcon from '@mui/icons-material/Visibility';
import axios from "axios";
import { useUIProvider } from '../../context/Context';

const navListsDashboard = [
    {
        id: 1,
        navName: "dashboard",
        navTarget: "/dashboard",
        navsymbol: <DashboardIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 2,
        navName: "create course",
        navTarget: "/dashboard/create_course",
        navsymbol: <AddCircleIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 3,
        navName: "courses",
        navTarget: "/dashboard/view_courses",
        navsymbol: <VisibilityIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 4,
        navName: "users",
        navTarget: "/dashboard/users",
        navsymbol: <PersonIcon style={{ fontSize: "12px" }} />,
    },
]

const Dashboard = () => {
    const [adminData, setAdminData] = useState([]);
    const [subscriptionData, setSubscriptionData] = useState([]);
    const { toggle } = useUIProvider();
    let date = new Date().getDate();
    let month = new Date().getMonth() + 1;
    let year = new Date().getFullYear();
    let previousMonth = new Date().getMonth();
    let previousLastMonth = new Date().getMonth() - 1;

    date = date < 10 ? `${0}${date}` : date;

    const getDetailsDashboard = async () => {
        await axios.get("/dashboard/userdetails")
            .then(res => {
                setAdminData(res.data);
            })
            .catch(err => {
                setAdminData([]);
            })
    };

    const getDetailsDashboardsubscription = async () => {
        await axios.get("/subscription/bf/info")
            .then(res => {
                setSubscriptionData(res.data);
            })
            .catch(err => {
                setSubscriptionData([]);
            })
    }

    useEffect(() => {
        toggle && document.querySelector(".dashboard_page").classList.add("active");
        toggle && document.querySelectorAll(".dashboard_page_frist_title").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".dashboard_page_frist_num").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".dashboard_page_frist_percen").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".dashboard_second_progress_header").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".dashboard_second_progress_percen").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".dashboard_page_container_right_lists").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".dashboard_page").classList.remove("active");
        !toggle && document.querySelectorAll(".dashboard_page_frist_title").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".dashboard_page_frist_num").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".dashboard_page_frist_percen").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".dashboard_second_progress_header").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".dashboard_second_progress_percen").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".dashboard_page_container_right_lists").forEach(e => e.classList.remove("active"));

        getDetailsDashboard();
        getDetailsDashboardsubscription();
    }, [toggle]);


    return (
        <div className="dashboard_page">
            <div className="dashboard_page_container">
                <div className="dashboard_page_container_left">

                    <div className="dashboard_page_frist">

                        <div className="dashboard_page_frist_box">
                            <h3 className="dashboard_page_frist_title">
                                users
                            </h3>
                            <div className="dashboard_page_frist_container">
                                <h3 className="dashboard_page_frist_num">
                                    {
                                        adminData && Object.keys(adminData).length ?
                                            adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                            :
                                            Object.keys(adminData).length
                                    }
                                </h3>
                                <p className="dashboard_page_frist_percen">
                                    {
                                        Math.floor(
                                            adminData && Object.keys(adminData).length &&
                                                adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length ?
                                                (
                                                    (
                                                        adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                            /
                                                            adminData ? adminData.filter((e) => e.createdAt.substring(5, 7) <= `${previousMonth}` & e.createdAt.substring(5, 7) >= `${previousLastMonth}`).length : 1
                                                    )
                                                    /
                                                    adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                )
                                                *
                                                100
                                                :
                                                Object.keys(adminData).length
                                        )
                                    }
                                    %
                                    <span>
                                        <ArrowUpwardIcon
                                            style={{
                                                fontSize: "14px",
                                                color: "#389c53",
                                                marginLeft: "10px",
                                            }}
                                        />
                                    </span>
                                </p>
                            </div>
                            <p className="dashboard_page_frist_discription">
                                since last month
                            </p>
                        </div>

                        <div className="dashboard_page_frist_box">
                            <h3 className="dashboard_page_frist_title">
                                subscription
                            </h3>
                            <div className="dashboard_page_frist_container">
                                <h3 className="dashboard_page_frist_num">
                                    {
                                        subscriptionData && Object.keys(subscriptionData).length &&
                                            adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                            ?
                                            subscriptionData.length
                                            :
                                            Object.keys(subscriptionData).length
                                    }
                                </h3>
                                <p className="dashboard_page_frist_percen">
                                    {
                                        Math.floor(subscriptionData && Object.keys(subscriptionData).length &&
                                            subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length ?
                                            (
                                                (
                                                    subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                        /
                                                        subscriptionData ? subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${previousMonth}` & e.createdAt.substring(5, 7) >= `${previousLastMonth}`).length : 1
                                                )
                                                /
                                                subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                            )
                                            *
                                            100
                                            :
                                            Object.keys(subscriptionData).length
                                        )
                                    }
                                    %
                                    <span>
                                        <ArrowUpwardIcon
                                            style={{
                                                fontSize: "14px",
                                                color: "#389c53",
                                                marginLeft: "10px",
                                            }}
                                        />
                                    </span>
                                </p>
                            </div>
                            <p className="dashboard_page_frist_discription">
                                since last month
                            </p>
                        </div>
                    </div>

                    <div className="dashboard_page_second">
                        <div className="dashboard_second_progress_box">

                            <div className="dashboard_second_progress_title">
                                <h4 className="dashboard_second_progress_header">users</h4>
                                <span className="dashboard_second_progress_percen">
                                    {
                                        Math.floor(Math.floor(adminData && Object.keys(adminData).length &&
                                            adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length ?
                                            (
                                                (
                                                    adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                        /
                                                        adminData ? adminData.filter((e) => e.createdAt.substring(5, 7) <= `${previousMonth}` & e.createdAt.substring(5, 7) >= `${previousLastMonth}`).length : 1
                                                )
                                                /
                                                adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                            )
                                            *
                                            100
                                            :
                                            Object.keys(adminData).length
                                        )
                                        )
                                    }
                                    %</span>
                            </div>
                            <div className="dashboard_second_progress_bar">
                                <div
                                    style={{
                                        width: `${parseFloat(
                                            adminData && Object.keys(adminData).length &&
                                                adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length ?
                                                (
                                                    (
                                                        adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                            /
                                                            adminData ? adminData.filter((e) => e.createdAt.substring(5, 7) <= `${previousMonth}` & e.createdAt.substring(5, 7) >= `${previousLastMonth}`).length : 1
                                                    )
                                                    /
                                                    adminData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                )
                                                *
                                                100
                                                :
                                                Object.keys(adminData).length
                                        )}%`,
                                        backgroundColor: "#c011f5",
                                        position: "relative",
                                        height: "100%",
                                        borderRadius: ".5rem",
                                    }}></div>
                            </div>

                            <div className="dashboard_second_progress_title">
                                <h4 className="dashboard_second_progress_header">subscription</h4>
                                <span className="dashboard_second_progress_percen">
                                    {
                                        Math.floor(subscriptionData && Object.keys(subscriptionData).length &&
                                            subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length ?
                                            (
                                                (
                                                    subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                        /
                                                        subscriptionData ? subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${previousMonth}` & e.createdAt.substring(5, 7) >= `${previousLastMonth}`).length : 1
                                                )
                                                /
                                                subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                            )
                                            *
                                            100
                                            :
                                            Object.keys(subscriptionData).length
                                        )
                                    }

                                    %</span>
                            </div>
                            <div className="dashboard_second_progress_bar">
                                <div
                                    style={{
                                        width: `${parseFloat(
                                            subscriptionData && Object.keys(subscriptionData).length &&
                                                subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length ?
                                                (
                                                    (
                                                        subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                            /
                                                            subscriptionData ? subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${previousMonth}` & e.createdAt.substring(5, 7) >= `${previousLastMonth}`).length : 1
                                                    )
                                                    /
                                                    subscriptionData.filter((e) => e.createdAt.substring(5, 7) <= `${month}` & e.createdAt.substring(5, 7) >= `${previousMonth}`).length
                                                )
                                                *
                                                100
                                                :
                                                Object.keys(subscriptionData).length
                                        )}%`,
                                        backgroundColor: "#c011f5",
                                        position: "relative",
                                        height: "100%",
                                        borderRadius: ".5rem",
                                    }}></div>
                            </div>

                        </div>

                        <div className="dashboard_second_chart_box">
                            <Piechart adminData={adminData} subscriptionData={subscriptionData} />
                        </div>
                    </div>

                </div>
                <div className="dashboard_page_container_right">
                    <AdminRight navLists={navListsDashboard} />
                </div>
            </div>
        </div>
    )
}

export default Dashboard;