import React, { useEffect, useState } from 'react';
import "./Users.css";
import AdminRight from '../../components/adminright/AdminRight';
import DeleteIcon from '@mui/icons-material/Delete';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import DashboardIcon from '@mui/icons-material/Dashboard';
import PersonIcon from '@mui/icons-material/Person';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import VisibilityIcon from '@mui/icons-material/Visibility';
import CircleIcon from '@mui/icons-material/Circle';
import { useNavigate } from 'react-router-dom';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { useUIProvider } from '../../context/Context';

const navListsDashboard = [
    {
        id: 1,
        navName: "dashboard",
        navTarget: "/dashboard",
        navsymbol: <DashboardIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 2,
        navName: "create course",
        navTarget: "/dashboard/create_course",
        navsymbol: <AddCircleIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 3,
        navName: "courses",
        navTarget: "/dashboard/view_courses",
        navsymbol: <VisibilityIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 4,
        navName: "users",
        navTarget: "/dashboard/users",
        navsymbol: <PersonIcon style={{ fontSize: "12px" }} />,
    },
]

const Users = () => {
    const [allusers, setAllusers] = useState([]);
    const [rolechange, setRolechange] = useState(false);
    const [role, setRole] = useState("user");
    const [userId, setUserId] = useState(0);
    const navigate = useNavigate();
    const { toggle } = useUIProvider();

    const getAllUsers = async () => {
        await axios.get(`/dashboard/userdetails`)
            .then(res => {
                setAllusers(res.data);
            })
            .catch(err => {
                setAllusers([]);
            })
    }

    // console.log(allusers);

    const changeRole = async (id) => {
        setRolechange(change => !change);

        rolechange ? setRole("admin") : setRole("user");

        await axios.post(`/dashboard/userdetails/${id}`, {
            role
        })
            .then(res => {
                getAllUsers();
                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    const deleteUser = async (id) => {
        await axios.delete(`/dashboard/userdetails/${id}`)
            .then(res => {
                getAllUsers();

                localStorage.removeItem("login_profile_id");
                localStorage.removeItem("login_dashboard");
                localStorage.removeItem("login_profile");

                navigate("/login");

                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    useEffect(() => {
        toggle && document.querySelector(".dashboard_page").classList.add("active");
        toggle && document.querySelector(".all_users_page_header").classList.add("active");
        toggle && document.querySelectorAll(".table_body_row_cell").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".dashboard_page").classList.remove("active");
        !toggle && document.querySelector(".all_users_page_header").classList.remove("active");
        !toggle && document.querySelectorAll(".table_body_row_cell").forEach(e => e.classList.remove("active"));

        getAllUsers();

        const isActivte = localStorage.getItem("login_profile_id");
        setUserId(isActivte);
    }, [toggle])

    return (
        <div className="dashboard_page">
            <ToastContainer
                position="top-center"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <div className="dashboard_page_container">
                <div className="dashboard_page_container_left">
                    <div className="all_users_page">
                        <div className="container">
                            <h3 className="all_users_page_header">all users</h3>
                            <TableContainer
                                sx={{
                                    overflowX: "scroll",
                                    width: "80%",
                                    "&::-webkit-scrollbar": {
                                        width: "0.2rem",
                                    },
                                    "&::-webkit-scrollbar-track": {
                                        backgroundColor: "transparent",
                                        width: "0.2rem",
                                    },
                                    "&::-webkit-scrollbar-thumb": {
                                        background: "rgba(110, 106, 89, 0.4)",
                                        width: "0.2rem",
                                        borderRadius: "5rem",
                                    }
                                }}
                                className="table_container_box"
                            >
                                <Table className="table_container">
                                    <TableHead className="table_head">
                                        <TableRow className="table_head_row">
                                            <TableCell className="table_head_row_cell">name</TableCell>
                                            <TableCell className="table_head_row_cell">email</TableCell>
                                            <TableCell className="table_head_row_cell">role</TableCell>
                                            <TableCell className="table_head_row_cell">activate</TableCell>
                                            <TableCell className="table_head_row_cell">actions</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody className="table_body">
                                        {
                                            allusers && Object.keys(allusers).length &&
                                            allusers.map((user) => {
                                                return (
                                                    <TableRow key={user._id} className="table_body_row">
                                                        <TableCell className="table_body_row_cell">{user.fname}</TableCell>
                                                        <TableCell className="table_body_row_cell">{user.email}</TableCell>
                                                        <TableCell className="table_body_row_cell">{user.role}</TableCell>
                                                        <TableCell className="table_body_row_cell">
                                                            {
                                                                user._id === userId ?
                                                                    <>
                                                                        <CircleIcon
                                                                            sx={{
                                                                                color: "green",
                                                                            }}
                                                                        />
                                                                    </>
                                                                    :
                                                                    <>
                                                                        <CircleIcon
                                                                            sx={{
                                                                                color: "red",
                                                                            }}
                                                                        />
                                                                    </>
                                                            }
                                                        </TableCell>
                                                        <TableCell className="td_btns table_body_row_cell">
                                                            <button
                                                                type="button"
                                                                className="btn1"
                                                                onClick={() => changeRole(user._id)}
                                                            >change role</button>
                                                            <button
                                                                type="button"
                                                                className="btn2"
                                                                onClick={() => deleteUser(user._id)}
                                                            >
                                                                <DeleteIcon style={{ fontSize: "18px" }} />
                                                            </button>
                                                        </TableCell>
                                                    </TableRow>
                                                )
                                            })
                                        }
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </div>
                    </div>
                </div>

                <div className="dashboard_page_container_right">
                    <AdminRight navLists={navListsDashboard} />
                </div>
            </div>
        </div>
    )
}

export default Users;