import React, { useEffect, useState } from 'react';
import "./Piechart.css";
import { Chart } from "react-google-charts";
import { useUIProvider } from '../../context/Context';

const Piechart = ({ adminData, subscriptionData }) => {
    const { toggle } = useUIProvider();
    const [backColor, setBackColor] = useState("");

    const userSubscribe = [
        ['task', 'total'],
        ['subscribe', subscriptionData && Object.keys(subscriptionData).length],
        ['notsubscribe', adminData && Object.keys(adminData).length]
    ];

    useEffect(() => {
        toggle ? setBackColor("#3c3c3c") : setBackColor("#ffffff")
    }, [toggle])

    return (
        <div className="piechart_container">
            <Chart
                chartType='PieChart'
                data={userSubscribe}
                options={{
                    title: "Users",
                    pieHole: 0.4,
                    colors: ["#9460db", "#fc62f0"],
                    backgroundColor: `${backColor}`,
                }}
                style={{
                    color: `${backColor}`,
                }}
                className="piechart"
            />
        </div>
    )
}

export default Piechart;