import React, { useEffect, useState } from 'react';
import "./ContactUs.css";
import { useUIProvider } from '../../context/Context';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";

const ContactUs = () => {
    const { toggle } = useUIProvider();
    const [reqNewCourse, setReqNewCourse] = useState({
        fname: "",
        email: "",
        coursename: "",
    });

    const sendNewReqOnChange = (e) => {
        const { value, name } = e.target;

        setReqNewCourse({
            ...reqNewCourse,
            [name]: value,
        })
    }

    const sendNewReq = async () => {
        if (!reqNewCourse.fname | !reqNewCourse.email | !reqNewCourse.coursename) {
            setReqNewCourse({
                ...reqNewCourse,
                fname: "",
                email: "",
                coursename: "",
            })
        } else {
            await axios.post(`/api/new/contactus`, {
                fname: reqNewCourse.fname,
                email: reqNewCourse.email,
                coursename: reqNewCourse.coursename,
            })
                .then(res => {
                    toast.success(`Contact send successfully.`, {
                        position: "top-center",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "light",
                    });

                    setReqNewCourse({
                        ...reqNewCourse,
                        fname: "",
                        email: "",
                        coursename: "",
                    })
                })
                .catch(err => {
                    toast.error(`Can not send contact!`, {
                        position: "top-center",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "light",
                    });
                })
        }
    }

    useEffect(() => {
        toggle && document.querySelector(".contactus_page").classList.add("active");
        toggle && document.querySelector(".contactus_page_head").classList.add("active");

        toggle && document.querySelector(".fname").classList.add("active");
        toggle && document.querySelector(".fnameIp").classList.add("active");

        toggle && document.querySelector(".email").classList.add("active");
        toggle && document.querySelector(".emailIp").classList.add("active");

        toggle && document.querySelector(".coursename").classList.add("active");
        toggle && document.querySelector(".coursenameIp").classList.add("active");

        !toggle && document.querySelector(".contactus_page").classList.remove("active");
        !toggle && document.querySelector(".contactus_page_head").classList.remove("active");

        !toggle && document.querySelector(".fname").classList.remove("active");
        !toggle && document.querySelector(".fnameIp").classList.remove("active");

        !toggle && document.querySelector(".email").classList.remove("active");
        !toggle && document.querySelector(".emailIp").classList.remove("active");

        !toggle && document.querySelector(".coursename").classList.remove("active");
        !toggle && document.querySelector(".coursenameIp").classList.remove("active");
    }, [toggle]);

    return (
        <div className="contactus_page">
            <ToastContainer
                position="top-center"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <div className="container">
                <h3 className='contactus_page_head'>contact us</h3>
                <form method="POST">
                    <div className="form_content">
                        <label id='fname' className='fname'>Name</label>
                        <input
                            className='fnameIp'
                            id="fname"
                            name='fname'
                            type="text"
                            onChange={sendNewReqOnChange}
                            autoComplete='off'
                        />
                    </div>
                    <div className="form_content">
                        <label id='email' className='email'>Email Address</label>
                        <input
                            className='emailIp'
                            id="email"
                            name='email'
                            type="email"
                            onChange={sendNewReqOnChange}
                            autoComplete='off'
                        />
                    </div>
                    <div className="form_content">
                        <label id='coursename' className='coursename'>Message</label>
                        <textarea
                            className='coursenameIp'
                            id="coursename"
                            name='coursename'
                            type="text"
                            rows={5}
                            onChange={sendNewReqOnChange}
                            autoComplete='off'
                        />
                    </div>
                    <button type="button" onClick={sendNewReq} >Send Mail</button>
                </form>
            </div>
        </div>
    )
}

export default ContactUs;