import React from 'react';
import ErrorImg from "../../assets/error.png";
import "./ErrorPage.css";

const ErrorPage = () => {
    return (
        <div
            style={{
                minHeight: "100vh",
                width: "100%",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
            }}
        >
            <div
                style={{
                    minHeight: "18rem",
                    width: "18rem",
                }}
            >
                <img
                    src={ErrorImg}
                    alt={ErrorImg}
                    style={{
                        minHeight: "100%",
                        width: "100%",
                    }}
                    className="error_img"
                />
            </div>
        </div>
    )
}

export default ErrorPage;