import React, { useEffect, useState } from 'react';
import "./AddVideos.css";
import DeleteIcon from '@mui/icons-material/Delete';
import { useUIProvider } from '../../context/Context';
import axios from 'axios';
import { useNavigate, useParams } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Box, IconButton, List, ListItem } from '@mui/material';

const AddVideos = () => {
    const { course, setCourse, toggle } = useUIProvider();
    const { course_id } = useParams();
    const [videoDetails, setVideoDetails] = useState({
        videoTitle: "",
        videolist: "",
    });
    const [visible, setVisible] = useState(0);
    const [showDeleteBtn, setShowDeleteBtn] = useState(false);
    const navigate = useNavigate();

    const addVideoOnChange = (e) => {
        const { name, value } = e.target;

        setVideoDetails({
            ...videoDetails,
            [name]: value,
        })
    }

    const addVideoFileOnChange = (e) => {
        setVideoDetails({
            ...videoDetails,
            videolist: e.target.files[0],
        })
    }

    const getInfoToAddVideo = async (courseId) => {
        await axios.get(`/admin/dashboard/view_course/${courseId}`)
            .then(res => {
                setCourse(res.data);
            })
            .catch(err => {
                setCourse([]);
            })
    }

    const addvideoinfo = async (e) => {
        let formData = new FormData();

        if (!videoDetails.videoTitle | !videoDetails.videolist) {
            toast.error(`Please Fill The Deatails!`, {
                position: "top-center",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            });
        } else {
            formData.append("videolist", videoDetails.videolist, videoDetails.videolist.name);
            formData.append("videoTitle", videoDetails.videoTitle);


            await axios.post(`/admin/dashboard/add_video/${course_id}`,
                formData
            ).then(res => {
                getInfoToAddVideo(course_id);

                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });

                setVideoDetails({
                    videolist: "",
                    videoTitle: "",
                });

            }).catch(err => {
                toast.error(`Can Not Added Video Successfully!`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
        }
    }

    const deleteVideo = async (id) => {
        await axios.put(`/admin/dashboard/remove_video/${course_id}`,
            { videoid: id }
        )
            .then(res => {
                getInfoToAddVideo(course_id);

                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    const showBtn = async (id) => {
        localStorage.setItem("show_id", id);
        const isShow = localStorage.getItem("show_id");
        setVisible(isShow);
        setShowDeleteBtn(true);

    }

    const disableBtn = async () => {
        const isRemoving = localStorage.getItem("show_id");

        if (isRemoving) {
            const isRemove = localStorage.removeItem("show_id");
            setVisible(isRemove);
            setShowDeleteBtn(false);
        }
    }

    const watchVideo = (url) => {
        navigate(url);
    }

    useEffect(() => {
        toggle && document.querySelector(".add_videos_page").classList.add("active");
        toggle && document.querySelector(".add_videos_page_head").classList.add("active");
        toggle && document.querySelector(".videoTitle").classList.add("active");
        toggle && document.querySelector(".videoTitleIp").classList.add("active");
        toggle && document.querySelector(".videoUrl").classList.add("active");
        toggle && document.querySelector(".videoUrlIp").classList.add("active");
        toggle && document.querySelectorAll(".table_row_body_cell").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".add_videos_page").classList.remove("active");
        !toggle && document.querySelector(".add_videos_page_head").classList.remove("active");
        !toggle && document.querySelector(".videoTitle").classList.remove("active");
        !toggle && document.querySelector(".videoTitleIp").classList.remove("active");
        !toggle && document.querySelector(".videoUrl").classList.remove("active");
        !toggle && document.querySelector(".videoUrlIp").classList.remove("active");
        !toggle && document.querySelectorAll(".table_row_body_cell").forEach(e => e.classList.remove("active"));

        getInfoToAddVideo(course_id);
    }, [toggle]);

    return (
        <div className="add_videos_page">
            <ToastContainer
                position="top-center"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <h3 className='add_videos_page_head'>add videos</h3>
            <div className="container">
                <div className="add_videos_page_left">
                    <table>
                        <thead>
                            <tr>
                                <th>video name</th>
                                <th>video list</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                course && Object.keys(course).length &&
                                <tr className='table_row_body'>
                                    <td
                                        className='table_row_body_cell'
                                        onClick={() => watchVideo(`/videos/${course_id}`)}
                                        style={{
                                            cursor: "pointer",
                                        }}
                                    >
                                        {course.courseTitle}
                                    </td>
                                    <td
                                        className='table_row_body_cell'
                                        style={{
                                            width: "100%",
                                            height: "100%",
                                            position: "relative",
                                        }}
                                    >
                                        <List
                                            sx={{
                                                width: "100%",
                                                height: "100%",
                                                position: "relative",
                                            }}
                                        >
                                            {
                                                course.videolist && Object.keys(course.videolist).length ?
                                                    course.videolist.map((e, i) => {
                                                        return (
                                                            <ListItem
                                                                sx={{
                                                                    listStyle: "none",
                                                                    position: "relative",
                                                                    cursor: "pointer",
                                                                }}
                                                                key={e.i}
                                                                onMouseEnter={() => showBtn(e.videoid)}
                                                                onMouseLeave={disableBtn}
                                                            >
                                                                <Box
                                                                    sx={{
                                                                        listStyle: "none",
                                                                        textAlign: "center",
                                                                        width: "100%",
                                                                        height: "100%",
                                                                        fontSize: "18px",
                                                                        position: "relative",
                                                                    }}
                                                                >
                                                                    {e.videoTitle}
                                                                </Box>
                                                                {
                                                                    showDeleteBtn && visible === e.videoid ?
                                                                        <IconButton
                                                                            type="button"
                                                                            sx={{
                                                                                position: "absolute",
                                                                                top: 0,
                                                                                right: 0,
                                                                            }}
                                                                            onClick={() => deleteVideo(e.videoid)}
                                                                        >
                                                                            <DeleteIcon style={{ fontSize: "14px" }} />
                                                                        </IconButton>
                                                                        :
                                                                        <></>
                                                                }
                                                            </ListItem>
                                                        )
                                                    })
                                                    :
                                                    "No Videos Yet"
                                            }
                                        </List>
                                    </td>
                                </tr>
                            }
                        </tbody>
                    </table>
                </div>
                <div className="add_videos_page_right">
                    <form method="POST">
                        <div className="form_container">
                            <label id="videoTitle" className='videoTitle'>video name</label>
                            <input
                                id="videoTitle"
                                onChange={addVideoOnChange}
                                value={videoDetails.videoTitle}
                                name="videoTitle"
                                type="text"
                                autoComplete='off'
                                className='videoTitleIp'
                            />
                        </div>
                        <div className="form_container">
                            <label id="videoUrl" className='videoUrl'>add video</label>
                            <input
                                id="videoUrl"
                                onChange={addVideoFileOnChange}
                                name="videoUrl"
                                type="file"
                                className='videoUrlIp'
                            />
                        </div>
                        <button type="button" onClick={addvideoinfo}>upload</button>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default AddVideos;