import React, { useEffect, useState } from 'react';
import "./ProfilePage.css";
import { Avatar, Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, IconButton, Input } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import EmptyCart from "../../assets/emptycart.png";
import { useNavigate } from 'react-router-dom';
import { useUIProvider } from '../../context/Context';

const ProfilePage = () => {
    const [userDetails, setUserDetails] = useState([]);
    const [image, setImage] = useState("");
    const [imagePrev, setImagePrev] = useState("");
    const [showDialog, setShowDialog] = useState(false);
    const [showDialogProInfo, setShowDialogProInfo] = useState(false);
    const [showDialogProInfoPass, setShowDialogProInfoPass] = useState(false);
    const [profileInfo, setProfileInfo] = useState({
        fname: "",
        email: "",
        mobNum: "",
        password: "",
    })
    const [allCoursesPlaylist, setAllCoursesPlaylist] = useState([]);
    const navigate = useNavigate();
    const [isCourseSubscribe, setIsCourseSubscribe] = useState([]);
    const { toggle, setProfile } = useUIProvider();

    const findUser = async () => {
        await axios.get(`/profile/userdetails`)
            .then(res => {
                setUserDetails(res.data.user);
            })
            .catch(err => {
                setProfile(err.response.data.profile);
                setUserDetails([]);
            });
    }

    const paymentCourseFull = async () => {
        await axios.get(`/validate/watch/course`)
            .then(res => {
                setIsCourseSubscribe(res.data.map(e => e._id));
            })
            .catch(err => {
                setIsCourseSubscribe([]);
            })
    }

    const handleUpdateProImg = (e) => {
        const file = e.target.files[0];
        const reader = new FileReader();

        reader.readAsDataURL(file);

        reader.onloadend = () => {
            setImagePrev(reader.result);
            setImage(file);
        }
    }

    const updateProfileImage = async () => {
        const idUserUpdateImgProfile = localStorage.getItem("login_profile_id");
        let formData = new FormData();

        image.name && formData.append("courseImg", image, image.name) && formData.append("userId", idUserUpdateImgProfile);

        await axios.post("/profile/update/profilephoto", formData)
            .then(res => {
                setShowDialog(false);
                findUser();

                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
                setShowDialog(false);
            })
    }

    const getSpecificUser = async () => {
        await axios.get("/profile/update/profile")
            .then(res => {
                setProfileInfo({
                    ...profileInfo,
                    fname: res.data.fname,
                    email: res.data.email,
                    mobNum: res.data.mobNum,
                });
            })
            .catch(err => {
                setProfileInfo({
                    ...profileInfo,
                    fname: "",
                    email: "",
                    mobNum: "",
                });
            })
    }

    const handleOnChangeUserInfoProfile = (e) => {
        const { value, name } = e.target;

        setProfileInfo({
            ...profileInfo,
            [name]: value,
        })
    }

    const updateSpecificUserInfoProfile = async () => {
        await axios.post("/profile/update/av/details/profile_information", {
            fname: profileInfo.fname,
            email: profileInfo.email,
            mobNum: profileInfo.mobNum,
        })
            .then(res => {
                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
                setProfileInfo({
                    ...profileInfo,
                    fname: "",
                    email: "",
                    mobNum: "",
                });
                findUser();
                setShowDialogProInfo(false);
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    const getSpecificUserPassword = async () => {
        await axios.get("/profile/update/profile")
            .then(res => {
                setProfileInfo({
                    ...profileInfo,
                    password: res.data.password,
                });
            })
            .catch(err => {
                setProfileInfo({
                    ...profileInfo,
                    password: "",
                });
            })
    }

    const handleOnChangeUserInfoProfilePass = (e) => {
        const { value, name } = e.target;

        setProfileInfo({
            ...profileInfo,
            [name]: value,
        });
    }

    const updateSpecificUserInfoProfilePass = async () => {
        await axios.post("/profile/update/av/details/profile_information_password", {
            password: profileInfo.password,
        })
            .then(res => {
                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
                setProfileInfo({
                    ...profileInfo,
                    password: "",
                });
                findUser();
                setShowDialogProInfoPass(false);
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    const getAllCoursesFromPlaylist = async () => {
        await axios.get("/add/course/av2/toplaylist")
            .then(res => {
                setAllCoursesPlaylist(res.data.playlist);
            })
            .catch(err => {
                setAllCoursesPlaylist([]);
            })
    }

    const deleteCourseFromPlaylist = async (id) => {
        await axios.put(`/delete/course/av3/toplaylist`, {
            deletecourseid: id
        })
            .then(res => {
                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
                getAllCoursesFromPlaylist();
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    const selectCourse = (id) => {
        navigate(`/videos/${id}`);
    }

    const buyCourse = async (amount, title, description, courseid) => {
        let key;

        await axios.get("/getkey")
            .then(res => {
                key = res.data.key;
            })
            .catch(err => {
                key = "";
            });

        await axios.post("/payment/bs1/integrate/checkout", { amount, courseid })
            .then(res => {

                const options = {
                    "key": key,
                    "amount": res.data.data.amount,
                    "currency": "INR",
                    "name": res.data.user.fname,
                    "description": `${title}-${description.substring(0, 20)}`,
                    "image": `/public/photos/${res.data.user.image}`,
                    "order_id": res.data.data.id,
                    "callback_url": `/api/payment/verification/${courseid}`,
                    "prefill": {
                        "name": res.data.user.fname,
                        "email": res.data.user.email,
                        "contact": res.data.user.mobNum,
                    },
                    "notes": {
                        "address": res.data.user.email,
                    },
                    "theme": {
                        "color": "#3399cc"
                    }
                };

                const razor = new window.Razorpay(options);
                razor.open();
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    useEffect(() => {
        toggle && document.querySelector(".profile_page").classList.add("active");
        toggle && document.querySelectorAll(".profile_page_title").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".profile_info_creator_title").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".profile_info_creator_email").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".profile_info_creator_created").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".profile_second_title").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".profile_second_description").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".profile_second_category").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".profile_page").classList.remove("active");
        !toggle && document.querySelectorAll(".profile_page_title").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".profile_info_creator_title").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".profile_info_creator_email").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".profile_info_creator_created").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".profile_second_title").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".profile_second_description").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".profile_second_category").forEach(e => e.classList.remove("active"));

        console.log(userDetails.image);
        findUser();
        getSpecificUser();
        getSpecificUserPassword();
        getAllCoursesFromPlaylist();
        paymentCourseFull();
    }, [toggle]);

    return (
        <div className="profile_page">
            <ToastContainer
                position="top-center"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <div className="container">
                {userDetails && Object.keys(userDetails).length &&
                    <>
                        <h3 className="profile_page_title">profile</h3>
                        <div className="profile_frist">
                            <div className="profile_img_container">
                                <div className="profile_img">
                                    <Avatar className="img" src={`/public/photos/${userDetails.image}` || userDetails.image} alt={userDetails.image} />
                                </div>
                                <button type="button" className="profile_img_update" onClick={() => setShowDialog(true)}>update photo</button>
                            </div>
                            <div className="profile_info_creator">
                                <h3 className="profile_info_creator_title">{userDetails.fname}</h3>
                                <p className="profile_info_creator_email">Email : {userDetails.email}</p>
                                <p className="profile_info_creator_created">Created At : {userDetails.createdAt.substring(0, 10)}</p>
                                <div className="profile_info_creator_btns">
                                    <button
                                        type="button"
                                        className="btn1"
                                        onClick={() => {
                                            setShowDialogProInfo(true);
                                            getSpecificUser();
                                        }}
                                    >update profile</button>
                                    <button
                                        type="button"
                                        className="btn2"
                                        onClick={() => {
                                            setShowDialogProInfoPass(true);
                                            getSpecificUserPassword();
                                        }}
                                    >change password</button>
                                </div>
                            </div>
                        </div>
                        <h3 className="profile_second_title">playlist</h3>
                        <div className="profile_second">
                            {
                                allCoursesPlaylist && Object.keys(allCoursesPlaylist).length ?
                                    allCoursesPlaylist.map(e => {
                                        return (
                                            <div className="profile_second_info" key={e._id}>
                                                <div className="profile_second_img">
                                                    <img src={`/public/photos/${e.courseImg}`} alt={e.courseImg} />
                                                </div>
                                                <h3 className="profile_second_title">{e.courseTitle}</h3>
                                                <p className="profile_second_description">{e.courseDescription.substring(0, 40)}...</p>
                                                <p className="profile_second_category">{e.courseCategory}</p>
                                                <div className="profile_second_btns">

                                                    <button
                                                        type="button"
                                                        className="btn1"
                                                        style={{
                                                            display: isCourseSubscribe.find(cur => cur === e.courseid) ? "inline" : "none",
                                                        }}
                                                        onClick={() => selectCourse(e.courseid)}
                                                    >watch now</button>

                                                    <button
                                                        type="button"
                                                        className="buy_course"
                                                        style={{
                                                            display: isCourseSubscribe.find(cur => cur === e.courseid) ? "none" : "inline",
                                                        }}
                                                        onClick={() => buyCourse(
                                                            parseInt((e.courseDiscount === 0 ? e.coursePrice : (e.coursePrice - (e.coursePrice * e.courseDiscount) / 100))),
                                                            e.courseTitle,
                                                            e.courseDescription,
                                                            e.courseid
                                                        )}
                                                    >Buy Course</button>

                                                    <IconButton
                                                        className="btn2"
                                                        onClick={() => deleteCourseFromPlaylist(e.courseid)}
                                                    >
                                                        <DeleteIcon />
                                                    </IconButton>
                                                </div>
                                            </div>
                                        )
                                    })
                                    :
                                    <Box
                                        sx={{
                                            width: "auto",
                                            height: "auto",
                                            display: "flex",
                                            justifyContent: "center",
                                            alignItems: "center",
                                        }}
                                    >
                                        <img
                                            style={{
                                                width: "100%",
                                                height: "100%",
                                                opacity: 0.2,

                                            }}
                                            src={EmptyCart}
                                            alt={EmptyCart}
                                        />
                                    </Box>
                            }
                        </div>
                        <>
                            {/* Profile Photo Dialog */}
                            {showDialog && Object.keys(showDialog) &&
                                <Dialog
                                    open={showDialog}
                                    onClose={() => setShowDialog(false)}
                                    aria-labelledby="form-dialog-title"
                                    BackdropProps={{
                                        style: { backgroundColor: "rgba(124,122,123,0.8)" }
                                    }}
                                >
                                    <DialogTitle
                                        sx={{
                                            background: "#f8d511",
                                            marginBottom: "20px",
                                        }}
                                    >Please Update You Profile Photo</DialogTitle>
                                    <DialogContent>
                                        <Box
                                            sx={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center",
                                                flexDirection: "column",
                                                width: "100%",
                                                height: "100%",
                                            }}
                                        >
                                            <Box
                                                sx={{
                                                    width: "8rem",
                                                    height: "8rem",
                                                    marginBottom: "20px",
                                                }}
                                            >
                                                <Avatar
                                                    src={imagePrev}
                                                    sx={{
                                                        width: "100%",
                                                        height: "100%",
                                                    }}
                                                />
                                            </Box>
                                            <Box
                                                sx={{
                                                    width: "100%",
                                                    height: "2rem",
                                                    marginBottom: "20px",
                                                }}
                                            >
                                                <Input
                                                    type="file"
                                                    name="image"
                                                    onChange={(e) => handleUpdateProImg(e)}
                                                    sx={{
                                                        width: "100%",
                                                        height: "100%",
                                                        border: "1px solid rgba(124,122,123,0.8)",
                                                        borderRadius: "5px",
                                                    }}
                                                />
                                            </Box>
                                            <Box
                                                sx={{
                                                    width: "100%",
                                                    height: "100%",
                                                }}
                                            ></Box>
                                        </Box>
                                        <DialogActions
                                            sx={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center",
                                            }}
                                        >
                                            <Button
                                                sx={{
                                                    width: "8rem",
                                                    height: "2rem",
                                                    color: "#000000",
                                                    background: "#f8d511",
                                                    textTransform: "capitalize",
                                                    transition: "0.3s linear",
                                                    "&:hover": {
                                                        color: "#000000",
                                                        background: "#f8d511",
                                                        letterSpacing: "1.5px",
                                                    }
                                                }}
                                                onClick={updateProfileImage}
                                            >Update Photo</Button>
                                        </DialogActions>
                                    </DialogContent>
                                </Dialog>
                            }

                            {/* Profile Info */}
                            {showDialogProInfo && Object.keys(showDialogProInfo) &&
                                <Dialog
                                    open={showDialogProInfo}
                                    onClose={() => setShowDialogProInfo(false)}
                                    aria-labelledby="form-dialog-title"
                                    BackdropProps={{
                                        style: { backgroundColor: "rgba(124,122,123,0.8)" }
                                    }}
                                >
                                    <DialogTitle
                                        sx={{
                                            display: "flex",
                                            justifyContent: "center",
                                            alignItems: "center",
                                            background: "#f8d511",
                                            marginBottom: "20px",
                                        }}
                                    >Update Your Profile Info</DialogTitle>
                                    <DialogContent>
                                        <Box
                                            sx={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center",
                                                flexDirection: "column",
                                                width: "100%",
                                                height: "100%",
                                            }}
                                        >
                                            <Box
                                                sx={{
                                                    width: "20rem",
                                                    height: "2rem",
                                                    marginBottom: "20px",
                                                }}
                                            >
                                                <Input
                                                    placeholder="Full Name"
                                                    sx={{
                                                        width: "100%",
                                                        height: "100%",
                                                        "&.MuiInput-input": {
                                                            background: "#f8d511"
                                                        }
                                                    }}
                                                    onChange={handleOnChangeUserInfoProfile}
                                                    name="fname"
                                                    type="text"
                                                    value={profileInfo.fname}
                                                />
                                            </Box>
                                            <Box
                                                sx={{
                                                    width: "20rem",
                                                    height: "2rem",
                                                    marginBottom: "20px",
                                                }}
                                            >
                                                <Input
                                                    placeholder="Email Address"
                                                    sx={{
                                                        width: "100%",
                                                        height: "100%",
                                                        "&.MuiInput-input": {
                                                            background: "#f8d511"
                                                        }
                                                    }}
                                                    onChange={handleOnChangeUserInfoProfile}
                                                    name="email"
                                                    type="text"
                                                    value={profileInfo.email}
                                                />
                                            </Box>
                                            <Box
                                                sx={{
                                                    width: "20rem",
                                                    height: "2rem",
                                                    marginBottom: "20px",
                                                }}
                                            >
                                                <Input
                                                    placeholder="Mobile Number"
                                                    sx={{
                                                        width: "100%",
                                                        height: "100%",
                                                        "&.MuiInput-input": {
                                                            background: "#f8d511"
                                                        }
                                                    }}
                                                    onChange={handleOnChangeUserInfoProfile}
                                                    name="mobNum"
                                                    type="number"
                                                    value={profileInfo.mobNum}
                                                />
                                            </Box>
                                        </Box>
                                        <DialogActions
                                            sx={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center",
                                            }}
                                        >
                                            <Button
                                                sx={{
                                                    width: "8rem",
                                                    height: "2rem",
                                                    color: "#000000",
                                                    background: "#f8d511",
                                                    textTransform: "capitalize",
                                                    transition: "0.3s linear",
                                                    "&:hover": {
                                                        color: "#000000",
                                                        background: "#f8d511",
                                                        letterSpacing: "1.5px",
                                                    }
                                                }}
                                                onClick={updateSpecificUserInfoProfile}
                                            >Update Info</Button>
                                        </DialogActions>
                                    </DialogContent>
                                </Dialog>
                            }

                            {/* Dialog For Change Password */}
                            {
                                showDialogProInfoPass && Object.keys(showDialogProInfoPass) &&
                                <Dialog
                                    open={showDialogProInfoPass}
                                    onClose={() => setShowDialogProInfoPass(false)}
                                    aria-labelledby="form-dialog-title"
                                    BackdropProps={{
                                        style: { backgroundColor: "rgba(124,122,123,0.8)" }
                                    }}
                                >
                                    <DialogTitle
                                        sx={{
                                            display: "flex",
                                            justifyContent: "center",
                                            alignItems: "center",
                                            background: "#f8d511",
                                            marginBottom: "20px",
                                        }}
                                    >Change Your Password</DialogTitle>
                                    <DialogContent>
                                        <Box
                                            sx={{
                                                width: "20rem",
                                                height: "2rem",
                                                marginBottom: "20px",
                                            }}
                                        >
                                            <Input
                                                placeholder="Password"
                                                sx={{
                                                    width: "100%",
                                                    height: "100%",
                                                    "&.MuiInput-input": {
                                                        background: "#f8d511"
                                                    }
                                                }}
                                                onChange={handleOnChangeUserInfoProfilePass}
                                                name="password"
                                                type="password"
                                            />
                                        </Box>
                                        <DialogActions
                                            sx={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center",
                                            }}
                                        >
                                            <Button
                                                sx={{
                                                    width: "10rem",
                                                    height: "2rem",
                                                    color: "#000000",
                                                    background: "#f8d511",
                                                    textTransform: "capitalize",
                                                    transition: "0.3s linear",
                                                    "&:hover": {
                                                        color: "#000000",
                                                        background: "#f8d511",
                                                        letterSpacing: "1.5px",
                                                    }
                                                }}
                                                onClick={updateSpecificUserInfoProfilePass}
                                            >Update Password</Button>
                                        </DialogActions>
                                    </DialogContent>
                                </Dialog>
                            }
                        </>
                    </>
                }
            </div>
        </div >
    )
}

export default ProfilePage;