import React, { useEffect, useState } from 'react';
import "./Login.css";
import { Avatar, IconButton, InputAdornment, OutlinedInput } from '@mui/material';
import { NavLink, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useUIProvider } from '../../context/Context';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

const Login = () => {
    const [logUser, setLogUser] = useState({
        email: "",
        password: "",
    });
    const history = useNavigate();
    const { setDashboard, toggle, setProfile } = useUIProvider();
    const [visible, setVisible] = useState(false);

    const onChangeLog = (e) => {
        const { name, value } = e.target;
        setLogUser({
            ...logUser,
            [name]: value,
        })
    }

    const userLogin = async () => {
        const { email, password } = logUser;

        if (!email || !password) {
            setLogUser({
                ...logUser,
                email: "",
                password: "",
            })
        }

        await axios.post(`/logindetails`, { email, password })
            .then(res => {
                if (res.data.thisAdmin) {
                    setDashboard(true);
                    localStorage.setItem("login_dashboard", res.data.thisAdmin);
                }

                if (res.data.profile) {
                    setProfile(res.data.profile);
                    localStorage.setItem("login_profile", res.data.profile);
                    localStorage.setItem("login_profile_id", res.data.data._id);
                } else {
                    setProfile(false);
                }

                toast.success(`user logined successfully`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
                setTimeout(() => {
                    history("/course");
                }, 1000);
                setLogUser({
                    ...logUser,
                    email: "",
                    password: "",
                })
            })
            .catch(err => {
                setProfile(err.response.data.profile);
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    const handleClickShowPassword = () => {
        setVisible(state => !state);
    }

    const loginWithGoogle = () => {
        window.open("http://localhost:8000/google/callback", "_self");
    }

    useEffect(() => {
        toggle && document.querySelector(".login_page").classList.add("active");
        toggle && document.querySelector(".login_page_head").classList.add("active");
        toggle && document.querySelectorAll(".email").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".emailIp").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".password").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".passwordIp").forEach(e => e.classList.add("active"));
        toggle && document.querySelector(".navigate").classList.add("active");

        !toggle && document.querySelector(".login_page").classList.remove("active");
        !toggle && document.querySelector(".login_page_head").classList.remove("active");
        !toggle && document.querySelectorAll(".email").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".emailIp").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".password").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".passwordIp").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelector(".navigate").classList.remove("active");
    }, [toggle]);

    return (
        <div className="login_page">
            <ToastContainer
                position="top-center"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <div className="container">
                <h3 className='login_page_head'>welcome to course</h3>
                <Avatar className='avatar' src="" />
                <form method="POST">
                    <div className="form_content">
                        <label id='email' className='email'>Email Address</label>
                        <input
                            autoComplete='off'
                            className='emailIp'
                            id="email"
                            name='email'
                            value={logUser.email}
                            onChange={onChangeLog}
                            type="email"
                        />
                    </div>
                    <div className="form_content">
                        <label id='password' className='password'>Password</label>
                        <OutlinedInput
                            id="outlined-adornment-password password"
                            autoComplete='off'
                            className='passwordIp'
                            name='password'
                            value={logUser.password}
                            sx={{
                                width: "100%",
                                height: "100%",
                                border: "1px solid #000000",
                                "&.Mui-focused": {
                                    border: "1px solid #000000",
                                }
                            }}
                            onChange={onChangeLog}
                            type={visible ? "text" : "password"}
                            endAdornment={
                                <InputAdornment
                                    position="end"
                                >
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        edge="end"
                                        sx={{
                                            position: "absolute",
                                            right: 9,
                                            top: "-11px",
                                        }}
                                    >
                                        {visible ? <VisibilityOffIcon /> : <VisibilityIcon />}
                                    </IconButton>
                                </InputAdornment>
                            }
                        />
                    </div>
                    <div className="log_btns">
                        <button type="button" onClick={userLogin}>Sign In</button>
                    </div>
                </form>
                <p className='navigate'>new user? <span><NavLink to="/register">sign up</NavLink></span> here</p>
            </div>
        </div>
    )
}

export default Login;