import React, { useEffect, useState } from 'react';
import "./DrawerWeb.css"
import { Divider, List, ListItem } from '@mui/material';
import { NavLink } from 'react-router-dom';
import LogoutIcon from '@mui/icons-material/Logout';
import DashboardIcon from '@mui/icons-material/Dashboard';
import { useUIProvider } from '../../context/Context';

const itemsWithProfile = [
    {
        id: 1,
        listName: "home",
        listTarget: "/",
    },
    {
        id: 2,
        listName: "browse all course",
        listTarget: "/course",
    },
    {
        id: 3,
        listName: "request a course",
        listTarget: "/request_new_course",
    },
    {
        id: 4,
        listName: "contact us",
        listTarget: "/contactus",
    },
    {
        id: 5,
        listName: "about",
        listTarget: "/aboutus",
    },
    {
        id: 6,
        listName: "profile",
        listTarget: "/profile",
    },
]

const itemsWithoutProfile = [
    {
        id: 1,
        listName: "home",
        listTarget: "/",
    },
    {
        id: 2,
        listName: "browse all course",
        listTarget: "/course",
    },
    {
        id: 3,
        listName: "request a course",
        listTarget: "/request_new_course",
    },
    {
        id: 4,
        listName: "contact us",
        listTarget: "/contactus",
    },
    {
        id: 5,
        listName: "about",
        listTarget: "/aboutus",
    },
]

const DrawerWeb = ({ logout, closeDrawer }) => {
    const { dashboard, setDashboard, profile, setProfile, toggle } = useUIProvider();

    // const logoutFromGoogle = () => {
        // window.open("http://localhost:8000/google/logout", "_self");
    // }

    useEffect(() => {
        const isProfile = localStorage.getItem("login_profile");
        setProfile(isProfile);
        !isProfile && setProfile(false);

        const isDashboard = localStorage.getItem("login_dashboard");
        setDashboard(isDashboard);

        toggle && document.querySelector(".drawer_web").classList.add("active");
        toggle && document.querySelector(".drawer_web_header").classList.add("active");
        toggle && document.querySelector(".divider").classList.add("active");
        toggle && document.querySelectorAll(".drawer_web_list_item").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".drawer_web").classList.remove("active");
        !toggle && document.querySelector(".drawer_web_header").classList.remove("active");
        !toggle && document.querySelector(".divider").classList.remove("active");
        !toggle && document.querySelectorAll(".drawer_web_list_item").forEach(e => e.classList.remove("active"));
    }, [profile, toggle]);

    return (
        <>
            <div className="drawer_web">
                <h3 className="drawer_web_header">course builder</h3>
                <Divider className="divider" />
                <List>
                    {
                        profile ?
                            itemsWithProfile.map((e, i) => {
                                return (
                                    <NavLink
                                        key={e.id}
                                        style={{
                                            cursor: "pointer",
                                            display: "flex",
                                            justifyContent: "center",
                                            textDecoration: "none",
                                            color: "#4e4c4c",
                                            "&:hover": {
                                                backgroundColor: "#e6e6e6",
                                            }
                                        }}
                                        to={e.listTarget}
                                        onClick={closeDrawer}
                                    >
                                        <ListItem
                                            sx={{
                                                cursor: "pointer",
                                                display: "flex",
                                                justifyContent: "flex-start",
                                            }}
                                            className="drawer_web_list_item"
                                        >
                                            {e.listName}
                                        </ListItem>
                                    </NavLink>
                                )
                            })
                            :
                            itemsWithoutProfile.map((e, i) => {
                                return (
                                    <NavLink
                                        key={e.id}
                                        style={{
                                            cursor: "pointer",
                                            display: "flex",
                                            justifyContent: "center",
                                            textDecoration: "none",
                                            color: "#4e4c4c",
                                            "&:hover": {
                                                backgroundColor: "#e6e6e6",
                                            }
                                        }}
                                        to={e.listTarget}
                                        onClick={closeDrawer}
                                    >
                                        <ListItem
                                            sx={{
                                                cursor: "pointer",
                                                display: "flex",
                                                justifyContent: "flex-start",
                                            }}
                                            className="drawer_web_list_item"
                                        >
                                            {e.listName}
                                        </ListItem>
                                    </NavLink>
                                )
                            })
                    }
                </List>
                <div className="drawer_web_btns">
                    <div className="btns_container">
                        <NavLink
                            style={{
                                textDecoration: "none",
                            }}
                            to="/login"
                            onClick={closeDrawer}
                        >
                            <button type="button" className="btn1">sign in</button>
                        </NavLink>

                        <NavLink
                            type="button"
                            className="btn2"
                            style={{
                                textDecoration: "none",
                                fontSize: "18px",
                            }}
                            onClick={() => {
                                logout();
                                closeDrawer();
                                // logoutFromGoogle();
                            }}
                            to={"/login"}
                        >
                            <LogoutIcon sx={{ fontSize: "14px", marginRight: "5px" }} />
                            <span>sign out</span>
                        </NavLink>
                    </div>
                    {
                        dashboard &&
                        <div className="btn_dashboard">
                            <NavLink
                                style={{
                                    textDecoration: "none",
                                }}
                                to="/dashboard"
                                onClick={closeDrawer}
                            >
                                <button type="button" className="btn3">
                                    <DashboardIcon sx={{ fontSize: "14px", marginRight: "5px" }} />
                                    <span>dashboard</span>
                                </button>
                            </NavLink>
                        </div>
                    }
                </div>
            </div>
        </>
    )
}

export default DrawerWeb;