import React, { useEffect } from 'react';
import "./HomePage.css";
import { NavLink } from 'react-router-dom';
import { useUIProvider } from '../../context/Context';

const HomePage = () => {
    const { toggle } = useUIProvider();

    useEffect(() => {
        toggle && document.querySelector(".home_page").classList.add("active");
        toggle && document.querySelector(".home_page .left_div .home_page_header").classList.add("active");
        toggle && document.querySelector(".home_page .left_div .home_page_des").classList.add("active");

        !toggle && document.querySelector(".home_page").classList.remove("active");
        !toggle && document.querySelector(".home_page .left_div .home_page_header").classList.remove("active");
        !toggle && document.querySelector(".home_page .left_div .home_page_des").classList.remove("active");
    }, [toggle])

    return (
        <div className="home_page">
            <div className="left_div">
                <h1 className="home_page_header">learn from the expert</h1>
                <p className="home_page_des">find value content at reasonable price</p>
                <NavLink
                    style={{
                        textDecoration: "none",
                    }}
                    to="/course"
                >
                    <button>explore now</button>
                </NavLink>
            </div>
            <div className="right_div">
                <img src="./homepage.jpg" alt="homepage" />
            </div>
        </div>
    )
}

export default HomePage;