import React, { useEffect, useState } from 'react';
import "./ViewCourses.css";
import AdminRight from '../../components/adminright/AdminRight';
import DeleteIcon from '@mui/icons-material/Delete';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import DashboardIcon from '@mui/icons-material/Dashboard';
import PersonIcon from '@mui/icons-material/Person';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import VisibilityIcon from '@mui/icons-material/Visibility';
import EmptyCart from "../../assets/emptycart.png";
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { useUIProvider } from '../../context/Context';

const navListsDashboard = [
    {
        id: 1,
        navName: "dashboard",
        navTarget: "/dashboard",
        navsymbol: <DashboardIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 2,
        navName: "create course",
        navTarget: "/dashboard/create_course",
        navsymbol: <AddCircleIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 3,
        navName: "courses",
        navTarget: "/dashboard/view_courses",
        navsymbol: <VisibilityIcon style={{ fontSize: "12px" }} />,
    },
    {
        id: 4,
        navName: "users",
        navTarget: "/dashboard/users",
        navsymbol: <PersonIcon style={{ fontSize: "12px" }} />,
    },
]

const ViewCourses = () => {
    const navigate = useNavigate();
    const [viweCourses, setViweCourses] = useState([]);
    const { toggle } = useUIProvider();

    const toAddVideos = async (id) => {
        navigate(`/dashboard/view_courses/add_videos/${id}`);
    }

    const getDetailsOfCourses = async () => {
        await axios.get(`/admin/dashboard/create_course`)
            .then(res => {
                setViweCourses(res.data);
            })
            .catch(err => {
                setViweCourses([]);
            })
    }

    const deleteCourse = async (id) => {
        await axios.get(`/admin/dashboard/delete_course/${id}`)
            .then(res => {
                getDetailsOfCourses();
            })
            .catch(err => {
                console.log(err);
            })
    }

    useEffect(() => {
        toggle && document.querySelector(".dashboard_page").classList.add("active");
        toggle && document.querySelector(".all_course_page_header").classList.add("active");
        toggle && document.querySelectorAll(".table_body_row_cell").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".dashboard_page").classList.remove("active");
        !toggle && document.querySelector(".all_course_page_header").classList.remove("active");
        !toggle && document.querySelectorAll(".table_body_row_cell").forEach(e => e.classList.remove("active"));
        
        getDetailsOfCourses();
    }, [toggle]);

    return (
        <div className="dashboard_page">
            <div className="dashboard_page_container">
                <div className="dashboard_page_container_left">
                    <div className="all_course_page">
                        <div className="container">
                            <h3 className="all_course_page_header">all course</h3>
                            <TableContainer
                                sx={{
                                    overflowX: "scroll",
                                    width: "80%",
                                    margin: 0,
                                    "&::-webkit-scrollbar": {
                                        width: "0.2rem",
                                    },
                                    "&::-webkit-scrollbar-track": {
                                        backgroundColor: "transparent",
                                        width: "0.2rem",
                                    },
                                    "&::-webkit-scrollbar-thumb": {
                                        background: "rgba(110, 106, 89, 0.4)",
                                        width: "0.2rem",
                                        borderRadius: "5rem",
                                    }
                                }}
                                className="table_container_box">
                                <Table sx={{
                                    // overflow: "hidden",
                                }} className="table_container">
                                    <TableHead className="table_head">
                                        <TableRow className="table_head_row">
                                            <TableCell className="table_head_row_cell">Poster</TableCell>
                                            <TableCell className="table_head_row_cell">Title</TableCell>
                                            <TableCell className="table_head_row_cell">Category</TableCell>
                                            <TableCell className="table_head_row_cell">Creator</TableCell>
                                            <TableCell className="table_head_row_cell">Views</TableCell>
                                            <TableCell className="table_head_row_cell">Lectors</TableCell>
                                            <TableCell className="table_head_row_cell">Actions</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody className="table_body">
                                        {
                                            viweCourses && Object.keys(viweCourses).length ?
                                                viweCourses.map((courseinfo) => {
                                                    return (
                                                        <TableRow key={courseinfo.courseid} className="table_body_row">
                                                            <TableCell
                                                                style={{
                                                                    width: "4rem",
                                                                    height: "2rem",
                                                                }}
                                                                className="table_body_row_cell"
                                                            >
                                                                <img
                                                                    src={`/public/photos/${courseinfo.courseImg}`}
                                                                    style={{ width: "100%", height: "100%", border: "none" }}
                                                                />
                                                            </TableCell>
                                                            <TableCell className="table_body_row_cell">{courseinfo.courseTitle}</TableCell>
                                                            <TableCell className="table_body_row_cell">{courseinfo.courseCategory}</TableCell>
                                                            <TableCell className="table_body_row_cell">{courseinfo.courseCreater}</TableCell>
                                                            <TableCell className="table_body_row_cell">{courseinfo.courseTitle}</TableCell>
                                                            <TableCell className="table_body_row_cell">{courseinfo.courseTitle}</TableCell>
                                                            <TableCell className="td_btns table_body_row_cell">
                                                                <button
                                                                    type="button"
                                                                    className="btn1"
                                                                    onClick={() => toAddVideos(courseinfo.courseid)}
                                                                >view lectures</button>
                                                                <button
                                                                    type="button"
                                                                    className="btn2"
                                                                    onClick={() => deleteCourse(courseinfo.courseid)}
                                                                >
                                                                    <DeleteIcon style={{ fontSize: "18px" }} />
                                                                </button>
                                                            </TableCell>
                                                        </TableRow>
                                                    )
                                                })
                                                :
                                                <TableRow>
                                                    <TableCell colSpan={7}>
                                                        <img
                                                            style={{
                                                                width: "auto",
                                                                height: "auto",
                                                                opacity: 0.3,
                                                            }}
                                                            src={EmptyCart}
                                                            alt={EmptyCart}
                                                        />
                                                    </TableCell>
                                                </TableRow>
                                        }
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </div>
                    </div>
                </div>

                <div className="dashboard_page_container_right">
                    <AdminRight navLists={navListsDashboard} />
                </div>
            </div>
        </div>
    )
}

export default ViewCourses;