import React from 'react';
import './App.css';
import { Routes, Route } from "react-router-dom";
import axios from 'axios';
import HomePage from './pages/homepage/HomePage';
import Navbaar from "./components/header/Navbaar";
import Login from './pages/loginpage/Login';
import Register from './pages/registerpage/Register';
import DrawerWeb from './components/drawer/DrawerWeb';
import { Drawer } from '@mui/material';
import Course from './pages/course/Course';
import RequestNewCourse from './pages/requestnewcourse/RequestNewCourse';
import ContactUs from './pages/contactus/ContactUs';
import AboutUs from './pages/aboutus/AboutUs';
import Footer from './components/footer/Footer';
import ProfilePage from './pages/profile/ProfilePage';
import Dashboard from './pages/dashboard/Dashboard';
import CreateCourse from './pages/createcourse/CreateCourse';
import ViewCourses from './pages/viewcourses/ViewCourses';
import Users from './pages/users/Users';
import AddVideos from './pages/addvideos/AddVideos';
import { useUIProvider } from './context/Context';
import Videolistpage from './pages/videolistpage/Videolistpage';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import PaymentSuccess from './pages/paymentsuccess/PaymentSuccess';
import ErrorPage from './pages/errorpage/ErrorPage';

function App() {
  const { drawerOpen, setDrawerOpen, setDashboard, setProfile } = useUIProvider();

  const drawerFun = () => {
    setDrawerOpen(true);
  }

  const closeDrawer = () => {
    setDrawerOpen(false);
  }
  const logout = async () => {
    await axios.get(`/logout`)
      .then(res => {
        closeDrawer();

        setDashboard(res.data.thisAdmin);
        localStorage.removeItem("login_dashboard", res.data.thisAdmin);

        setProfile(res.data.profile);
        localStorage.removeItem("login_profile", res.data.profile);
        localStorage.removeItem("login_profile_id");

        toast.success(`${res.data.message}`, {
          position: "top-center",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
        });

      })
      .catch(err => {
        closeDrawer();
        setDashboard(err.response.data.thisAdmin);
        setProfile(err.response.data.profile);
        toast.error(`${err.response.data.error}`, {
          position: "top-center",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
        });
      })
  }

  return (
    <>
      <ToastContainer
        position="top-center"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
      <Navbaar drawerFun={drawerFun} />
      <Drawer
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <DrawerWeb setDrawerOpen={setDrawerOpen} logout={logout} closeDrawer={closeDrawer} />
      </Drawer>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/course" element={<Course />} />
        <Route path="/request_new_course" element={<RequestNewCourse />} />
        <Route path="/contactus" element={<ContactUs />} />
        <Route path="/aboutus" element={<AboutUs />} />
        <Route path="/profile" element={<ProfilePage />} />
        <Route path="/dashboard" element={<Dashboard />} />
        <Route path="/dashboard/create_course" element={<CreateCourse />} />
        <Route path="/dashboard/view_courses" element={<ViewCourses />} />
        <Route path="/dashboard/view_courses/add_videos/:course_id" element={<AddVideos />} />
        <Route path="/videos/:course_id" element={<Videolistpage />} />
        <Route path="/dashboard/users" element={<Users />} />
        <Route path="/paymentsuccess" element={<PaymentSuccess />} />
        <Route path="*" element={<ErrorPage />} />
      </Routes>
      <Footer />
    </>
  );
}

export default App;
