import React, { useEffect, useState } from 'react';
import { Box, Divider, Drawer, List, ListItem } from '@mui/material';
import { NavLink, useLocation, useNavigate } from 'react-router-dom';
import "./AdminRight.css"
import { useUIProvider } from '../../context/Context';

const AdminRight = ({ navLists }) => {
    const { drawerAdminOpen, setDrawerAdminOpen, toggle } = useUIProvider();
    const [drawerBackCol, setDrawerBackCol] = useState("");
    const [drawerCol, setDrawerCol] = useState("");
    const [selectPath, setSelectPath] = useState("");
    const location = useLocation();
    const navigate = useNavigate();

    const onActive = (navTitle) => {
        navigate(`${navTitle}`);
        setSelectPath(`${location.pathname}`);
    }

    useEffect(() => {
        toggle ? setDrawerBackCol("#3c3c3c") : setDrawerBackCol("#ffffff");
        toggle ? setDrawerCol("#ffffff") : setDrawerCol("#3c3c3c");

        toggle && document.querySelectorAll(".dashboard_page_container_right_lists").forEach(e => e.classList.add("active"));

        !toggle && document.querySelectorAll(".dashboard_page_container_right_lists").forEach(e => e.classList.remove("active"));

        setSelectPath(`${location.pathname}`);
    }, [toggle, location, onActive]);
    
    return (
        <>
            {
                drawerAdminOpen &&
                <Drawer
                    open={drawerAdminOpen}
                    onClose={() => setDrawerAdminOpen(false)}
                    anchor="right"
                >
                    <Box
                        sx={{
                            backgroundColor: `${drawerBackCol}`,
                            margin: 0,
                            width: 250,
                            height: "100%",
                            overflowX: "hidden",
                        }}
                    >
                        <h3
                            className="drawer_admin_right_header"
                            style={{
                                color: `${drawerCol}`,
                            }}
                        >
                            Course Builder
                        </h3>
                        <Divider
                            style={{
                                backgroundColor: `${drawerCol}`,
                                margin: "0px 3px",
                            }}
                        />
                        <List
                            sx={{
                                width: "100%",
                                height: "3rem",
                            }}
                            className="drawer_admin_right_list"
                        >
                            {
                                navLists.map((e, index) => {
                                    return (
                                        <>
                                            {
                                                e.navName && Object.keys(e.navName).length &&
                                                <ListItem
                                                    className="dashboard_page_container_right_links"
                                                    sx={{
                                                        width: "100%",
                                                        height: "3rem",
                                                        color: selectPath === e.navTarget ? `${drawerBackCol}` : `${drawerCol}`,
                                                        backgroundColor: selectPath === e.navTarget ? `${drawerCol}` : `${drawerBackCol}`,
                                                        display: "flex",
                                                        justifyContent: "flex-start",
                                                        alignItems: "center",
                                                        textTransform: "capitalize",
                                                        fontSize: "18px",
                                                        transition: "linear 0.3s",
                                                        cursor: "pointer",
                                                        "&:hover": {
                                                            backgroundColor: `${drawerCol}`,
                                                            color: `${drawerBackCol}`,
                                                        }
                                                    }}
                                                    key={e.id}
                                                    onClick={() => {
                                                        onActive(e.navTarget);
                                                        setDrawerAdminOpen(false);
                                                    }}
                                                >
                                                    {e.navsymbol}
                                                    {e.navName}
                                                </ListItem>
                                            }
                                        </>
                                    )
                                })
                            }
                        </List>
                    </Box>
                </Drawer>
            }
            {
                <List
                    sx={{
                        width: "100%",
                        height: "3rem",
                    }}>
                    {
                        navLists.map((e, index) => {
                            return (
                                <>
                                    {
                                        e.navName && Object.keys(e.navName).length &&
                                        <ListItem
                                            className="dashboard_page_container_right_lists"
                                            sx={{
                                                width: "100%",
                                                height: "3rem",
                                                color: selectPath === e.navTarget ? `${drawerBackCol}` : `${drawerCol}`,
                                                display: "flex",
                                                justifyContent: "flex-start",
                                                alignItems: "center",
                                                textTransform: "capitalize",
                                                fontSize: "18px",
                                                cursor: "pointer",
                                                transition: "linear 0.3s",
                                                backgroundColor: selectPath === e.navTarget ? `${drawerCol}` : `${drawerBackCol}`,
                                                "&:hover": {
                                                    backgroundColor: `${drawerCol}`,
                                                    color: `${drawerBackCol}`,
                                                }
                                            }}
                                            key={e.id}
                                            onClick={() => onActive(e.navTarget)}
                                        >
                                            {e.navsymbol}
                                            {e.navName}
                                        </ListItem>
                                    }
                                </>
                            )
                        })
                    }
                </List>
            }
        </>
    )
}

export default AdminRight