import React, { useEffect } from 'react';
import { NavLink, useSearchParams } from 'react-router-dom';
import "./PaymentSuccess.css";
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { useUIProvider } from "../../context/Context";

const PaymentSuccess = () => {
    const searchRef = useSearchParams()[0];
    const { toggle } = useUIProvider();

    useEffect(() => {
        toggle && document.querySelector(".payment_success_container").classList.add("active");
        toggle && document.querySelector(".payment_success_container_head").classList.add("active");
        toggle && document.querySelector(".payment_success_container_ref").classList.add("active");
        toggle && document.querySelector(".payment_success_container_link").classList.add("active");

        !toggle && document.querySelector(".payment_success_container").classList.remove("active");
        !toggle && document.querySelector(".payment_success_container_head").classList.remove("active");
        !toggle && document.querySelector(".payment_success_container_ref").classList.remove("active");
        !toggle && document.querySelector(".payment_success_container_link").classList.remove("active");
    }, [toggle]);

    return (
        <div className="payment_success_container">
            <h3 className='payment_success_container_head'><CheckCircleIcon sx={{ color: "#4a965e", fontSize: "40px" }} />order successful</h3>
            <p className='payment_success_container_ref'>{searchRef.get("reference")}</p>
            <NavLink to={"/course"} className="payment_success_container_link">back to course?</NavLink>
        </div>
    )
}

export default PaymentSuccess