import React, { useEffect, useState } from 'react';
import "./Register.css";
import { Avatar } from '@mui/material';
import { NavLink, useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";
import { useUIProvider } from '../../context/Context';

const Register = () => {
    const [user, setUser] = useState({
        fname: "",
        email: "",
        mobNum: "",
        password: "",
        role: "",
        avatar: "",
    });
    const [imagePrev, setImagePrev] = useState("");
    const history = useNavigate();
    const { toggle } = useUIProvider();

    const handleRegister = (e) => {
        ;
        const { value, name } = e.target;

        setUser({
            ...user,
            [name]: value,
        });
    }

    const handleRegisterFile = (e) => {
        const file = e.target.files[0];
        const reader = new FileReader();
        reader.readAsDataURL(file);

        reader.onloadend = () => {
            setImagePrev(reader.result);
            setUser({
                ...user,
                avatar: file,
            });
        }
    }

    const uploadRegisterData = async (e) => {
        e.preventDefault();

        let formData = new FormData();

        const { fname, email, mobNum, password, avatar } = user;
        if (!fname || !email || !mobNum || !password || !avatar) {
            setUser({
                ...user,
                fname: "",
                email: "",
                mobNum: "",
                password: "",
                role: "",
                avatar: "",
            })
        } else {
            formData.append("image", user.avatar, user.avatar.name);
            formData.append("fname", user.fname)
            formData.append("email", user.email);
            formData.append("mobNum", user.mobNum);
            formData.append("role", "user");
            formData.append("password", user.password);
        }

        await axios.post(`/registerdetails`, formData)
            .then(res => {
                toast.success(`user register successfully`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
                setTimeout(() => {
                    history("/login");
                }, 1000);
                setUser({
                    ...user,
                    fname: "",
                    email: "",
                    mobNum: "",
                    password: "",
                    role: "",
                    avatar: "",
                })
            })
            .catch(err => {
                toast.error(`${err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })

    }

    useEffect(() => {
        toggle && document.querySelector(".regiter_page").classList.add("active");
        toggle && document.querySelector(".regiter_page_head").classList.add("active");
        toggle && document.querySelector(".fname").classList.add("active");
        toggle && document.querySelector(".fnameIp").classList.add("active");
        toggle && document.querySelector(".email").classList.add("active");
        toggle && document.querySelector(".emailIp").classList.add("active");
        toggle && document.querySelector(".mobNum").classList.add("active");
        toggle && document.querySelector(".mobNumIp").classList.add("active");
        toggle && document.querySelector(".password").classList.add("active");
        toggle && document.querySelector(".passwordIp").classList.add("active");
        toggle && document.querySelector(".avatarlogo").classList.add("active");
        toggle && document.querySelector(".avatarIp").classList.add("active");
        toggle && document.querySelector(".navigate").classList.add("active");
        
        !toggle && document.querySelector(".regiter_page").classList.remove("active");
        !toggle && document.querySelector(".regiter_page_head").classList.remove("active");
        !toggle && document.querySelector(".fname").classList.remove("active");
        !toggle && document.querySelector(".fnameIp").classList.remove("active");
        !toggle && document.querySelector(".email").classList.remove("active");
        !toggle && document.querySelector(".emailIp").classList.remove("active");
        !toggle && document.querySelector(".mobNum").classList.remove("active");
        !toggle && document.querySelector(".mobNumIp").classList.remove("active");
        !toggle && document.querySelector(".password").classList.remove("active");
        !toggle && document.querySelector(".passwordIp").classList.remove("active");
        !toggle && document.querySelector(".avatarlogo").classList.remove("active");
        !toggle && document.querySelector(".avatarIp").classList.remove("active");
        !toggle && document.querySelector(".navigate").classList.remove("active");
    }, [toggle]);

    return (
        <div className="regiter_page">
            <ToastContainer
                position="top-center"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <div className="container">
                <h3 className='regiter_page_head'>register</h3>
                <Avatar className='avatar' src={imagePrev} />
                <form method="POST">
                    <div className="form_content">
                        <label id='fname' className='fname'>Name</label>
                        <input autoComplete='off' className='fnameIp' id="fname" name='fname' value={user.fname} type="text" onChange={handleRegister} />
                    </div>
                    <div className="form_content">
                        <label id='email' className='email'>Email Address</label>
                        <input autoComplete='off' className='emailIp' id="email" name='email' value={user.email} type="email" onChange={handleRegister} />
                    </div>
                    <div className="form_content">
                        <label id='mobNum' className='mobNum'>Mobile No</label>
                        <input autoComplete='off' className='mobNumIp' id="mobNum" name='mobNum' value={user.mobNum} type="number" onChange={handleRegister} />
                    </div>
                    <div className="form_content">
                        <label id='password' className='password'>Password</label>
                        <input autoComplete='off' className='passwordIp' id="password" name='password' value={user.password} type="password" onChange={handleRegister} />
                    </div>
                    <div className="form_content">
                        <label id='avatar' className='avatarlogo'>Choose Avatar</label>
                        <input autoComplete='off' className='avatarIp' id="avatar" type="file" onChange={handleRegisterFile} />
                    </div>
                    <button type="button" onClick={uploadRegisterData}>Sign Up</button>
                </form>
                <p className="navigate">Already sign Up? <span><NavLink to="/login">login</NavLink></span> here</p>
            </div>
        </div>
    )
}

export default Register;