import React, { useEffect, useState } from 'react';
import "./Course.css";
import axios from 'axios';
import { Box } from '@mui/material';
import EmptyCart from '../../assets/emptycart.png';
import { useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useUIProvider } from '../../context/Context';

const filterBtn = [
    {
        id: 1,
        btnName: "web development",
    },
    {
        id: 2,
        btnName: "artificial intelligence",
    },
    {
        id: 3,
        btnName: "data structure & algoritham",
    },
    {
        id: 4,
        btnName: "app development",
    },
    {
        id: 5,
        btnName: "all",
    },
];

const Course = () => {
    const [courseList, setCourseList] = useState([]);
    const [searchCourse, setSearchCourse] = useState("");
    const navigate = useNavigate();
    const [isCourseSubscribe, setIsCourseSubscribe] = useState([]);
    const { toggle } = useUIProvider();
    const [loading, setLoading] = useState(false);

    const getDataCourse = async () => {
        await axios.get("/getallcourses")
            .then(res => {
                setTimeout(() => {
                    setLoading(true);
                    setCourseList(res.data);
                    setLoading(false);
                }, 1000);
            })
            .catch(err => {
                setCourseList([]);
            })
    }

    const paymentCourseFull = async () => {
        await axios.get("/validate/watch/course")
            .then(res => {
                setIsCourseSubscribe(res.data.map(e => e._id));
            })
            .catch(err => {
                setIsCourseSubscribe([]);
            })
    }

    const filterSelection = async (currentEle) => {
        if (currentEle === "all") {
            getDataCourse();
        } else if (currentEle) {
            await axios.get("/getallcourses")
                .then(res => {
                    setCourseList(res.data.filter(item => {
                        return item.courseCategory === currentEle;
                    }));
                })
                .catch(err => {
                    setCourseList([]);
                });
        }
    }

    const selectCourse = (id) => {
        navigate(`/videos/${id}`);
    }

    const addToPlayList = async (id) => {
        axios.get(`/add/course/av1/toplaylist/${id}`)
            .then(res => {
                getDataCourse();

                toast.success(`${res.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
            .catch(err => {
                toast.error(`${err.response.data.error || err.response.data.message}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            })
    }

    const buyCourse = async (amount, title, description, courseid) => {
        let key;

        await axios.get("/getkey")
            .then(res => {
                key = res.data.key;
            })
            .catch(err => {
                key = "";
            });

        await axios.post("/payment/bs1/integrate/checkout", { amount, courseid })
            .then(res => {

                const options = {
                    "key": key,
                    "amount": res.data.data.amount,
                    "currency": "INR",
                    "name": res.data.user.fname,
                    "description": `${title}-${description.substring(0, 20)}`,
                    "image": `/public/photos/${res.data.user.image}`,
                    "order_id": res.data.data.id,
                    "callback_url": `/api/payment/verification/${courseid}`,
                    "prefill": {
                        "name": res.data.user.fname,
                        "email": res.data.user.email,
                        "contact": res.data.user.mobNum,
                    },
                    "notes": {
                        "address": res.data.user.email,
                    },
                    "theme": {
                        "color": "#3399cc"
                    }
                };

                const razor = new window.Razorpay(options);
                razor.open();
            })
            .catch(err => {
                toast.error(`${err.response.data.message || err.response.data.error}`, {
                    position: "top-center",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                });
            });
    }

    useEffect(() => {
        toggle && document.querySelector(".course_page").classList.add("active");
        toggle && document.querySelector(".course_page_header").classList.add("active");
        toggle && document.querySelector(".course_input").classList.add("active");
        toggle && document.querySelector(".course_page_input").classList.add("active");
        toggle && document.querySelectorAll(".course_content_title").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".course_content_description").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".course_content_creater").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".course_content_lecture").forEach(e => e.classList.add("active"));
        toggle && document.querySelectorAll(".course_box").forEach(e => e.classList.add("active"));

        !toggle && document.querySelector(".course_page").classList.remove("active");
        !toggle && document.querySelector(".course_page_header").classList.remove("active");
        !toggle && document.querySelector(".course_input").classList.remove("active");
        !toggle && document.querySelector(".course_page_input").classList.remove("active");
        !toggle && document.querySelectorAll(".course_content_title").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".course_content_description").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".course_content_creater").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".course_content_lecture").forEach(e => e.classList.remove("active"));
        !toggle && document.querySelectorAll(".course_box").forEach(e => e.classList.remove("active"));

        getDataCourse();
        paymentCourseFull();
    }, [toggle]);

    return (
        <div className="course_page">
            <ToastContainer
                position="top-center"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
            <div className="course_page_container">
                <h3 className="course_page_header">All Courses</h3>
                <div className="course_input">
                    <input
                        className="course_page_input"
                        placeholder="Search here..."
                        value={searchCourse}
                        onChange={(e) => setSearchCourse(e.target.value)}
                    />
                </div>
                <div className="course_page_btns">
                    {
                        filterBtn.map((e) => {
                            return (
                                <button
                                    type="button"
                                    key={e.id}
                                    onClick={() => filterSelection(e.btnName ? e.btnName : "all")}
                                >{e.btnName}</button>
                            )
                        })
                    }
                </div>
                <div className="course_page_course_box">
                    <div className="course_page_course_list">
                        {
                            courseList && Object.keys(courseList).length ?
                                courseList
                                    .filter(e => searchCourse.toLowerCase() === "" ?
                                        e
                                        :
                                        e.courseCategory.toLowerCase().includes(searchCourse)
                                    )
                                    .map((e) => {
                                        return (
                                            <div className="course_box" key={e.courseid}>
                                                <div className="course_img">
                                                    <img src={`/public/photos/${e.courseImg}`} alt={e.courseImg} />
                                                </div>
                                                <div className="course_content">
                                                    <h3 className="course_content_title">{e.courseTitle}</h3>
                                                    <p className="course_content_description">{e.courseDescription.slice(0, 70)}..</p>
                                                    <p className="course_content_creater"><span>Creator : </span>{e.courseCreater}</p>
                                                    <p className="course_content_lecture">Lecture : {e.videolist.length ? e.videolist.length : 0}</p>
                                                    <div className="course_content_btns">

                                                        <button
                                                            type="button"
                                                            className="btn1"
                                                            style={{
                                                                display: isCourseSubscribe.find(cur => cur === e.courseid) ? "inline" : "none",
                                                            }}
                                                            onClick={() => selectCourse(e.courseid)}
                                                        >watch now</button>

                                                        <button
                                                            type="button"
                                                            className="btn2"
                                                            style={{
                                                                display: isCourseSubscribe.find(cur => cur === e.courseid) ? "none" : "inline",
                                                            }}
                                                            onClick={() => buyCourse(
                                                                parseInt((e.courseDiscount === 0 ? e.coursePrice : (e.coursePrice - (e.coursePrice * e.courseDiscount) / 100))),
                                                                e.courseTitle,
                                                                e.courseDescription,
                                                                e.courseid
                                                            )}
                                                        >Buy Course</button>

                                                        <button
                                                            type="button"
                                                            className="btn2"
                                                            onClick={() => addToPlayList(e.courseid)}
                                                        >add to playlist</button>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                :
                                <Box
                                    sx={{
                                        display: "flex",
                                        justifyContent: "center",
                                        alignItems: "center",
                                        width: "100%",
                                        height: '100%',
                                    }}
                                >
                                    <img
                                        src={EmptyCart}
                                        style={{
                                            width: "16rem",
                                            height: "18rem",
                                            objectFit: "cover",
                                            marginTop: "5rem",
                                            opacity: 0.2,
                                        }}
                                    />
                                </Box>
                        }
                    </div>
                </div>
            </div>
        </div >
    )
}

export default Course;