import React, { useEffect } from 'react';
import "./Footer.css";
import { useUIProvider } from '../../context/Context';

const Footer = () => {
    const { toggle } = useUIProvider();

    useEffect(() => {
        toggle && document.querySelector(".footer").classList.add("active");

        !toggle && document.querySelector(".footer").classList.remove("active");
    }, [toggle]);

    return (
        <div className="footer">
            <h3 className="footer_header" >all rights are reserved</h3>
            <p className="footer_des" >@ omkar ingawale</p>
        </div>
    )
}

export default Footer;